.header{
  background-color: white;
  position: relative;
}

.header__main-container{
  position: relative;
  text-align: center;
  margin:0 auto ;
  padding-top:20px;
  display:flex;
  justify-content: space-between;
  max-width: 1050px;
}

.header__logo--container{
  order:1;
  max-width: 33%;
}

.header__logo{
  cursor: pointer;
  max-width: 100%;
  height: auto;
  display: block;
}

.header__title{
  text-transform: uppercase;
  font-size: 3rem;
  min-width: 400px;
  order:2
}

.header__title__boss{
  display: block;
  font-size: 5rem;
  margin: 10px 0;
}

.header__img-rejestration{
  position: relative;
  text-align: left;
  display: inline-block;
  height: 150px;
  max-width: 250px;
  min-width: 140px;
  background-color: rgb(255, 144, 0);
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
  flex-grow: 3;
  order:3;
  align-self: flex-end;
}

.header__img-rejestration img{
  height: 30px;
  position: absolute;
  top: -20px;
  left: 60%;
}

.header__img-rejestration__info{
  height: 70%;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  margin-left: 20%;
  margin-top: 13%;
  font-size: 2rem;
}

.header__img-rejestration__info span:last-child{
  font-size: 1.5rem;
}

@media screen and (max-width: 750px){
  .header__main-container{
    flex-wrap: wrap;
  }
}

@media screen and (max-width: 600px){
  .header__main-container{
    flex-direction: column;
    padding:0
  }
  .header__img-rejestration{
    order:0;
    width: 100%;
    max-width: 100%;
    margin:0;
    height:auto;
  }
  .header__img-rejestration__info{
    height: auto;
    margin:0;
    flex-direction: row;
    align-items: center;
    justify-content: space-around;
  }
  .header__img-rejestration img{
    display:none
  }
}

@media (min-width: 1500px){
  .header__main-container{
    max-width: 1300px
  }
}

