.services__info{
  text-align: left;
  font-size: 1.5rem;
  padding: 20px;
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0;
  background-color: rgba(255,255,255,0.8);
}

.services__logo{
  position: relative;
  max-width: 100px;
  width: 100px;
}

.services__logo__rf{
  margin-left:-30px;
}

.services__list{
  display: flex;
  flex-direction: column;
}

.services__list__item{
  position: relative;
  display: flex;
  align-items: center;
  max-width: 600px;
  margin: 15px 0;
}

.services__list__item__description{
  padding: 10px 10px 10px 117px;
  font-size: 1.8rem;
  margin-left:-67px;
  background-color: rgb(209, 216, 73);
  border-radius: 20px;
  width:100%;
}

.services__legend--container{
  position: relative;
  display: flex;
  justify-content: flex-end;
}

.services__legend{
  width: 350px;
  font-size: 2rem;
  position: fixed;
  z-index: 10;
  background-color: rgba(255,255,255,0.8);
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0;
  padding: 20px;
  margin-top:40px;
}

.services__legend__logo{
  width:30px;
}

.services__legend__logo + span{
  margin-left:30px
}

.services__legend__line{
  width: 80px;
  height: 1px;
  background-color: rgb(255, 144, 0);
  margin: 10px 0;
}

.services__legend__rejestration{
  cursor: pointer;
  color: rgb(255, 144, 0);
  margin-top: 10px;
}

.services__list__item__description--clickable:hover{
  cursor: pointer;
  background-color: rgb(255, 144, 0);
}

.mail-modal__button{
  margin: 20px auto 0 auto;
  background-color: rgb(255, 144, 0);
  color: white;
  width: 100px;
  height: 30px;
  border-radius: 10px;
  font-size: 2rem;
  transition: 0.2s;
  cursor:pointer;
  user-select: none;

}

.mail-modal__button--close{
  background-color: grey;
}

.mail-modal__button:hover{
  background-color: rgb(209, 216, 73);
}


@media (max-width: 980px){
  .services__legend{
    position: relative;
  }

  .services__legend--container{
    justify-content: center;
  }
  .services__list__item{
    max-width: 900px;
  }
  .services__list__item{
    margin: 10px 0;
  }
}