.news-item{
  background-color: white;
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
    padding: 20px;
    margin: 30px 10px 30px 10px;
    padding-bottom: 50px;
}

.news-list{
  text-align: center;
}

.news-list h4 {
  color: rgb(198, 204, 69);
  font-size: 3rem;
}

.news-list h5 {
  font-size: 2rem;
  font-weight: bold;
}

.dotted-list {
  list-style: disc !important;
}

.news-item ul {
  text-align: left;
  margin: 40px 0;
  font-size: 14px;
}

.news-rejestration{
  display: flex;
}

@media (max-width: 950px){
  .news-rejestration img{
    display: none;
  }
}