.dashboard{
  position: relative;
  width:100%;
  display: flex;
}

.dashboard__doctors__title{
  background-color: rgb(140, 141, 130);
  padding:20px;
  margin-bottom: 22px;
  color: white;
  font-size: 24px;
}

.dashboard__doctors_buttons{
  background-color: rgb(209, 216, 73);
}

.dashboard__content-container{
  position: relative;
  margin-left: 20px;
  width:100%;
}

.dashboard__content{
  background-color: rgba(255,255,255,0.8);
  min-height: 452px;
  width:100%;
  padding: 20px;
  overflow: auto;
  overflow-x: hidden;
}

.dashboard__content div h2{
  margin: 0;
  padding-top: 20px;
  font-size: 30px;
  text-align: center;
}

.dashboard__content div p{
  text-align: left;
  padding: 30px;
  font-size: 14px;
  line-height: 1.42857143;
  color: #333;
}

.dashboard__content div a:hover{
  text-decoration: none;
}

@media (max-width: 800px){
  .dashboard{
    flex-direction: column;
  }
  .dashboard__content-container{
    margin: 20px 0;
  }
}
