.contact{
  display: flex;
  font-size: 14px;
  color:#333;
}

.contact__item{
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
  height: auto;
  width: 400px;
  margin: 40px auto;
  background-color: rgba(255,255,255,0.8);
  padding: 20px;
  text-align: center;
}

.contact h3{
  font-size: 4rem;
  margin: 20px auto 0 auto;
  font-weight: 500;

}

.contact__icon{
  font-size: 5rem;
  width:50px;
  margin: 15px 30px 15px 0;
  fill: #333;
}

.contact span{
  text-align: left;
}

@media (max-width:980px){
  .contact{
    flex-direction: column;
  }
}