*{
  box-sizing: border-box;
 font-family: Cuprum, sans-serif;
}

body{
  margin:0
}

html{
  font-size: 62.5%;
}

ul {
  text-decoration: none;
  list-style: none;
  padding: 0;
}

p, li{
  font-size: 14px;
  color:#333;
}


.App{
  max-width: 1050px;
  margin: auto;
  padding: 0;
}

.white {
  background-color: rgba(255,255,255,0.8);
  height: 150px;
}

.white h1 {
  text-transform: uppercase;
}

.flex-center{
  display: flex;
  justify-content: center;
  align-items: center;
}

.flex{
  display:flex
}

.align-center{
  align-items:center
}

.content{
  margin: 20px auto;
  position: relative;
}

.title{
  font-size: 3rem;
  margin: 0 0 0 30px;
}

.mail{
  color: rgb(255, 118, 0);
  font-size: 2rem;
  padding-left: 10px;
  cursor: pointer;
  text-decoration: none;
}

.card{
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0;
    background-color: rgba(255,255,255,0.9);
}

@media (min-width: 1500px){
  .App{
    max-width: 1300px
  }
}