.background{
  background-attachment: fixed;
  background-position: bottom;
  background-size: cover;
  height: 100vh;
  width:100vw;
  position: fixed;
  top:0;
  left:0;
  margin: 0;
  transition: background-image 1s;
}

.component__nav-button{
  font-size: 2.5rem;
  cursor: pointer;
  height: 100%;
  width: 150px;
  padding: 18px;
  transition: background-color 0.5s, color 0.2s;
  user-select: none;
}

.component__nav-button:hover{
  background-color: rgb(255, 144, 0);
  color: white;
}
.component__nav-button--active{
  background-color: rgb(255, 144, 0);
  color: white;
}

.component__doctor-button{
  border-bottom: 1px solid grey;
  padding: 20px 0px 20px 40px;
  font-size: 1.8rem;
  transition: background-color 0.5s, color 0.2s;
  cursor:pointer;
  min-width: 310px;
}

.component__doctor-button:last-child{
  border:none;
}

.component__doctor-button--active{
  background-color: rgb(255, 144, 0);
  color: white;
}

.component__doctor-button:hover{
  background-color: rgb(255, 144, 0);
  color: white;
}

.component__logo{
  background-color: white;
  border-radius: 100%;
  max-width: 100%;
}

.component__logo__rf{
position: absolute;
font-size: 4rem;
}

.component__logo__question__img{
  border-radius: 100%;
  max-width: 100%;
}

.component__logo__question__text{
  position: absolute;
  color: rgb(255, 144, 0);
  font-size: 7rem;
  font-weight: 500 !important;
}

.component__logo__question ~ .component__logo {
  background-color: red;
}

@media only screen and (max-width: 1100px){
  .component__nav-button{
    font-size: 2.2rem;
    width: 110px;
  }
}

@media only screen and (max-width: 800px){
  .component__nav-button{
    font-size: 2rem;
    width: 100px;
  }
}