.prices{
  margin-top: 50px;
  position: relative;
  text-align: left;
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0;
  background-color: rgba(255,255,255,0.7);
  padding: 40px;
}

.prices h2{
  font-size: 6rem;
  color:#333;
  text-align: center;
  font-weight: 500;
}

.prices h3{
  font-size: 3rem;
  color:#333;
  text-align: center;
  font-weight: 500;
  padding: 0 10%;
}

.prices hr {
  height: 1px;
  border: 0;
  border-top: 2px solid rgb(172, 178, 54);
  width: 400px;
  margin: 70px auto;
}

.prices__item{
  margin: 20px 0 50px 0;
  display: flex;
  justify-content: space-around;
}

.prices__item__service{
  width: 600px;
  padding: 8px 10px;
  font-size: 2rem;
  min-height: 70px;
  flex-direction: column;
}

.prices__item__service span{
  font-size: 1.5rem;
  color: rgb(255, 144, 0);
}

.prices__item__price{
  width: 200px;
  padding: 8px 10px 8px;
  font-size: 2rem;
  flex-direction: column;
  justify-content: space-around;

}

.prices__item__price span:first-child{
margin-top: 5px;
}

@media only screen and (max-width: 1290px){
  .prices__item__service{
    width:400px;
    font-size: 1.5rem;
  }
  .prices__item__price{
    width: 150px;
    font-size: 1.5rem;
  }
}

@media (max-width:580px){
  .prices h3{
    padding:0
  }
}