.tips{
  position: relative;
}

.tips__logo{
  width:100px;
}

.tips__documentation{
  text-align: left;
  background-color: rgba(255,255,255,0.8);
  padding: 20px;
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
  margin-bottom:20px;
}

.tips__documentation span{
  color: rgb(255, 144, 0);
  font-weight: bold;
}

.tips__documentation p, .tips__documentation li {
  padding-left: 10px;
}

.tips__documentation a{
  text-decoration: none;
  color: black;
}

.tips__documentation a:hover{
  color: rgb(255, 144, 0);
}